
.Header__menu{
    background-color: black;
    color: firebrick;
    padding: 7px;
}
.LogoMain, img{
    height: 40px;
    width: auto;

}

.Header__menu a:hover{
    color: ivory;
}

.Header__menu a{
    color: aquamarine;
}
.card-panel {

    width: auto;
    /*background: #61dafb;*/
    margin: 2px;



}


.CardSpecImg {
    max-width:100%;
    max-height:100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: cover;
    height: 180px;
    min-width: 100%;
    min-height: 100%;
    flex-shrink: 0;
}

.CardSpecPrice
{
position: absolute;
    inline-size: -webkit-fit-content;
    inline-size: -moz-fit-content;
    inline-size: fit-content;
    bottom: 76px;
    margin-left: 10px;
    padding: 0 10px;
    border-radius: 5px;
    background: #db2828;
    color: #fff;

}

.CardSpecTitle{
    font-size: 16px;
}
